import './App.css';
import { BrowserRouter, Navigate, Route, Routes } from "react-router-dom";
import { NavbarEj1 } from './Componentes/Layaut/NavbarEj1';
import { Inicio } from "./Componentes/Inicio";
import { Contacto } from "./Componentes/Contacto";
import { Sucursales } from './Componentes/Sucursales';
import { PiePag } from './Componentes/PiePag';
import { Categorias } from './Componentes/Categorias';
import { Ayuda } from './Componentes/Ayuda';
import { Educativos } from './Componentes/Educativos';
import { Juegos } from './Componentes/Juegos';
import { Marcas } from './Componentes/Marcas';
import { Novedades } from './Componentes/Novedades';
import { RecursosProfesores } from './Componentes/RecursosProfesores';




function App() {
  return (
    <div className="App">
      <BrowserRouter>
          <Routes>
            <Route path="/" element={<NavbarEj1 />}>
              <Route index element={<Inicio />  }  />
              <Route path="Contacto" element={<Contacto />} />
              <Route path="Sucursales" element={<Sucursales />} />
              <Route path="Categorias" element={<Categorias />} />
              <Route path="Ayuda" element={<Ayuda />} />
              <Route path="Educativos" element={<Educativos />} />
              <Route path="Juegos" element={<Juegos />} />
              <Route path="Marcas" element={<Marcas />} />
              <Route path="Novedades" element={<Novedades />} />
              <Route path="RecursosProfesores" element={<RecursosProfesores />} />
              <Route path="*" element={<Navigate replace to="/" />} />
            </Route>
          </Routes>
          <PiePag></PiePag>
        </BrowserRouter>
        
        
    </div>
  );
}

export default App;
