import React from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link, Outlet } from 'react-router-dom';
import img1 from '../img/img1.jpg'

export const NavbarEj1 = () => {
  return (
    <>
    <Navbar expand="lg" className="navbar bg-body-tertiary navbar-dark">
      <Container>
        <Navbar.Brand as={Link} to='/Inicio'>
        <img src={img1} alt="" height="50" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to='/Inicio'>INICIO</Nav.Link>
            <Nav.Link as={Link} to='/Sucursales'>SUCURSALES</Nav.Link>
            <Nav.Link as={Link} to='/Contacto'>CONTACTO</Nav.Link>
            <Nav.Link as={Link} to='/Categorias'>CATEGORIAS</Nav.Link>
            <Nav.Link as={Link} to='/Educativos'>EDUCATIVOS</Nav.Link>
            <Nav.Link as={Link} to='/Juegos'>JUEGOS</Nav.Link>
            <Nav.Link as={Link} to='/Marcas'>MARCAS</Nav.Link>
            <Nav.Link as={Link} to='/Novedades'>NOVEDADES</Nav.Link>
            <Nav.Link as={Link} to='/RecursosProfesores'>RECURSOS DE PROFESORES</Nav.Link>
            <Nav.Link as={Link} to='/Ayuda'>AYUDA</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    <section>
      <Outlet></Outlet>
    </section>
    </>
  );
};