import React from 'react'
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import img1 from '../Componentes/img/img15.avif';
import img2 from '../Componentes/img/img16.avif';
import img3 from '../Componentes/img/img17.avif';
import img4 from '../Componentes/img/img18.avif';

export const Juegos = () => {
  return (
    <div>
        <h1>JUEGOS DE EXTERIOR</h1>
        <div>
        <Row xs={1} md={2} className="g-4">

            <Card>
                <Card.Img variant="top" src={img1} />
                <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                    This is a longer card with supporting text below as a natural
                    lead-in to additional content. This content is a little bit
                    longer.
                </Card.Text>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img variant="top" src={img2} />
                <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                    This is a longer card with supporting text below as a natural
                    lead-in to additional content. This content is a little bit
                    longer.
                </Card.Text>
                </Card.Body>
            </Card>

        </Row>
        
        </div>
        <h1>JUEGOS DE MESA</h1>
        <div>
        <Row xs={1} md={2} className="g-4">

            <Card>
                <Card.Img variant="top" src={img3} />
                <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                    This is a longer card with supporting text below as a natural
                    lead-in to additional content. This content is a little bit
                    longer.
                </Card.Text>
                </Card.Body>
            </Card>
            <Card>
                <Card.Img variant="top" src={img4} />
                <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                    This is a longer card with supporting text below as a natural
                    lead-in to additional content. This content is a little bit
                    longer.
                </Card.Text>
                </Card.Body>
            </Card>

        </Row>
        
        </div>
    </div>
  )
}
